.blogs-item {
 display: flex;
 align-items: center;
 padding-bottom: 190px;
}

.blogs-content.left {
    margin-left: 70px;
}

.blogs-content.right {
    margin-right: 70px;
}

.blogs-content-title {
    font-family: 'Playfair Display', serif;
    font-style: italic;
    font-weight: 400;
    font-size: 40px;

    margin-top: 0;
}

.blogs-content p {
    font-family: 'Lato', sans-serif;
    color: #444;
    font-size: 16px;
    letter-spacing: 1px;
    line-height: 28px;

    margin-bottom: 65px;
}

.blogs-btn {
    font-family: 'Lato', sans-serif;
    color: #000;
    font-size: 16px;
    font-weight: 800;
    text-transform: capitalize;

    border: 1px solid #ccc;
    padding: 15px 25px;
    background: #fff;

    cursor: pointer;

    margin-top: 55px;

    transition: all 0.1s ease;

    &:hover {
        background-color: #000;
        border: 1px solid #000;
        color: #fff;
        transition: all 0.1s ease;
    }
}